.inventory {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-left: 100px;
  padding-right: 100px;
  padding-top: 150px;
}

.inventory_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 100%;
  text-align: center;
  color: #11244B;
  width: 100%;
  margin-bottom: 50px;
}

.inventory_item {
  flex-basis: 27.8%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inventory_img {
  margin-bottom: 30px;
}

.inventory_subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 120%;
  text-align: center;
  color: #11244B;
  margin-bottom: 18px;
}

.inventory_description {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 150%;
  text-align: center;
  color: #5D6387;
}

@media(max-width: 990px) {
  .inventory {
    padding-left: 0;
    padding-right: 0;
  }
}

@media(max-width: 767px) {
  .inventory {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 80px;
    flex-direction: column;
    justify-content: flex-start;
  }

  .inventory_title {
    font-size: 28px;
    line-height: 110%;
    margin-bottom: 30px;
  }

  .inventory_img {
    width: 200px;
    margin-bottom: 20px;
  }

  .inventory_subtitle {
    font-size: 18px;
    line-height: 120%;
    margin-bottom: 10px;
  }

  .inventory_item {
    margin-bottom: 30px;
  }
}