.clients {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: calc(150px - 30px);
}

.clients_title {
  font-family: Ubuntu;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 100%;
  text-align: center;
  color: #11244B;
  width: 100%;
  margin-bottom: 50px;
}

.clients_item {
  border: 1px solid #D6D6D6;
  border-radius: 6px;
  flex-basis: 23.1%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 25px 0;
  margin-bottom: 30px;
  box-sizing: border-box;
}

@media(max-width: 990px) {
  .clients_item {
    flex-basis: 30.4%;
  }
}

@media(max-width: 767px) {
  .clients {
    flex-direction: column;
    justify-content: flex-start;
    padding-bottom: calc(80px - 15px);
  }

  .clients_title {
    font-size: 28px;
    line-height: 110%;
    margin-bottom: 30px;
  }

  .clients_item {
    margin-bottom: 15px;
  }
}