.steps {
  margin-top: 120px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-right: 100px;
  padding-left: 100px;
}

.steps_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 100%;
  text-align: center;
  color: #11244B;
  width: 100%;
  margin-bottom: 50px;
}

.steps_item {
  flex-basis: 27.8%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.steps_img {
  margin-bottom: 26px;
}

.steps_subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 120%;
  text-align: center;
  color: #11244B;
  margin-bottom: 18px;
}

.steps_description {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 150%;
  text-align: center;
  color: #5D6387;
}

@media(max-width: 990px) {
  .steps {
    padding-right: 0;
    padding-left: 0;
  }
}

@media(max-width: 767px) {
  .steps {
    margin-top: 60px;
    flex-direction: column;
    justify-content: flex-start;
    padding-right: 15px;
    padding-left: 15px;
  }

  .steps_title {
    font-size: 28px;
    line-height: 110%;
    margin-bottom: 30px;
  }

  .steps_img {
    width: 60px;
    height: 60px;
    margin-bottom: 16px;
  }

  .steps_subtitle {
    font-size: 18px;
    line-height: 120%;
    margin-bottom: 12px;
  }

  .steps_item {
    margin-bottom: 30px;
  }
}