.discount {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #F3F7FF;
  padding: 120px 0;
}

.discount_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 48px;
  line-height: 120%;
  text-align: center;
  color: #11244B;
  margin-bottom: 35px;
}

.discount_subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 120%;
  text-align: center;
  color: #5D6387;
  margin-bottom: 45px;
}

.discount_button {
  background: #4A50ED;
  border-radius: 6px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 90%;
  text-align: center;
  color: #FFFFFF;
  padding: 22px 0;
  min-width: 240px;
  border: 1px solid #4A50ED;

  &:hover {
    cursor: pointer;
    background: white;
    color: #3A3A3A;
  }
}

@media(max-width: 767px) {
  .discount {
    padding: 80px 15px;
  }

  .discount_title {
    font-size: 28px;
    line-height: 110%;
    margin-bottom: 24px;
  }

  .discount_subtitle {
    font-size: 20px;
    line-height: 120%;
    margin-bottom: 28px;
  }
}