.banner {
  background: url('../img/banner_background.svg') no-repeat;
  background-size: cover;
  padding-top: calc(178px + 80px);
  padding-bottom: 200px;
  
  & .center {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.banner_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 48px;
  line-height: 100%;
  text-align: center;
  color: #11244B;
  margin-bottom: 40px;
}

.banner_subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 100%;
  text-align: center;
  color: #5D6387;
  margin-bottom: 60px;
}

.banner_form {
  background: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
  max-width: 1140px;
  width: 100%;
  box-sizing: border-box;
  box-shadow: 0px 10px 80px -30px rgba(74, 80, 237, 0.3);
  border-radius: 6px;
}

.banner_swing {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: #F8F8F8;
  border: 1px solid #D9D9D9;
  box-sizing: border-box;
  border-radius: 4px;
  flex-basis: 22.8%;
}

.banner_prompt {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 90%;
  text-align: center;
  color: #11244B;

  &--rooms {
    min-width: 117px;
  }

  &--bathrooms {
    min-width: 92px;
  }
}

.banner_decrease,
.banner_increase {
  background: transparent;
  border: none;
  width: 62px;
  height: 58px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0;

  &:hover {
    cursor: pointer;

    & path {
      stroke: #4A50ED;
    }
  }
}

.banner-wrapper {
  position: relative;
  flex-basis: 21.05%;

  & .valid-error {
    position: absolute;
    color: white;
    font-size: 11px;
    left: 0;
    bottom: -47px;
    background: #3F44C9;
    padding: 5px;
    display: none;

    &.active {
      display: block;
    }

    &::before {
      content: '';
      border: 8px solid transparent;
      border-bottom: 10px solid #3F44C9;
      position: absolute;
      top: -18px;
      left: 8px;
      transform: translateX(-50%);
    }
  }
}

.banner_text-input {
  background: #F8F8F8;
  border: 1px solid #D9D9D9;
  border-radius: 4px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 90%;
  color: black;
  appearance: none;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
  min-height: 60px;
  max-width: 240px;

  &::placeholder {
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 90%;
    color: #A8A8A8;
  }

  &.valid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat,  #F8F8F8;
  }

  &.invalid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 7L7 17' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M17 17L7 7' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A") 98% center no-repeat,  #F8F8F8;
  }
}

.banner_button {
  background: #4A50ED;
  border-radius: 4px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 90%;
  color: #FFFFFF;
  border: 1px solid #4A50ED;
  flex-basis: 23.24%;
  min-height: 60px;

  &:hover {
    cursor: pointer;
    background: white;
    color: #3A3A3A;
  }

  &:disabled {
    opacity: 0.3;

    &:hover {
      cursor: default;
      background: #4A50ED;
      color: #FFFFFF;
    }
  }
}

@media(max-width: 990px) {
  .banner {
    padding-top: calc(130px + 80px);
    padding-bottom: 140px;
  }

  .banner_title {
    margin-bottom: 25px;
  }

  .banner_form {
    flex-direction: column;
    max-width: 390px;
    width: 100%;
    padding: 30px
  }

  .banner_swing {
    margin-bottom: 15px;
  }

  .banner_text-input {
    max-width: none;
    width: 100%;
  }

  .banner_button {
    max-width: none;
    margin-top: 15px;
  }

  .banner-wrapper {
    & .valid-error {
      position: static;
      color: red;
      background: transparent;
      padding: 0;
      display: none;
  
      &.active {
        display: block;
      }
  
      &::before {
        display: none;
      }
    }
  }
}

@media(max-width: 767px) {
  .banner {
    padding-top: calc(50px + 60px);
    padding-bottom: 60px;
  }

  .banner_title {
    font-size: 30px;
    line-height: 110%;
    margin-bottom: 16px;
  }

  .banner_subtitle {
    font-size: 20px;
    line-height: 120%;
    margin-bottom: 30px;
  }

  .banner_form {
    padding: 30px 15px;
  }
}