.consultation {
  background: #4A50ED;
  padding: 120px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-right: 15px;
  padding-left: 15px;
}

.consultation_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 48px;
  line-height: 120%;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 35px;
}

.consultation_subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 120%;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 45px;
}

.consultation_button {
  box-shadow: 0px 0px 30px rgba(255, 255, 255, 0.4);
  border-radius: 6px;
  border: 1px solid white;
  background: white;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 90%;
  color: #4A50ED;
  padding: 22px 0;
  min-width: 220px;

  &:hover {
    cursor: pointer;
    border-color: white;
    box-shadow: none;
    background: #4A50ED;
    color: white;
  }
}

@media(max-width: 767px) {
  .consultation {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .consultation_title {
    font-size: 28px;
    line-height: 110%;
    margin-bottom: 24px;
  }

  .consultation_subtitle {
    font-size: 20px;
    line-height: 120%;
    margin-bottom: 30px;
  }
}

.modal.consultation {
  padding-top: 0;
  padding-bottom: 0;
}