.questions {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-right: 100px;
  padding-left: 100px;
  padding-top: 150px;
  padding-bottom: 150px;
}

.questions_title {
  font-family: Ubuntu;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 100%;
  text-align: center;
  color: #11244B;
  margin-bottom: 50px;
}

.questions_item {
  border-top: 1px solid #D9D9D9;
  border-bottom: 1px solid #D9D9D9;
  padding-top: 28px;
  padding-bottom: 14px;
}

.quesrions_checkbox {
  display: none;
}

.questions_question {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 120%;
  color: #11244B;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 14px;

  &:hover {
    cursor: pointer;
    color: #4A50ED;

    & svg {
      stroke: #4A50ED;
    }
  }

  & svg {
    transform: rotate(0);
    transition: transform 0.3s ease-in-out;
    flex-shrink: 0;
    margin-left: 5px;
  }
}

.question_answer {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 150%;
  color: #5D6387;
  padding-right: 100px;

  overflow: hidden;
  max-height: 0;
}

.quesrions_checkbox:checked + .questions_question svg {
  stroke: black;
  transform: rotate(180deg);
  transition: transform 0.3s ease-in-out;
}

.quesrions_checkbox:checked + .questions_question:hover svg {
  stroke: #4A50ED;
}

.quesrions_checkbox:checked ~ .question_answer {
  max-height: 100vh;
  transition: max-height 1s ease-in-out 0.05s;
}

@media(max-width: 990px) {
  .questions {
    padding-right: 0;
    padding-left: 0;
  }

  .question_answer {
    padding-right: 50px;
  }
}

@media(max-width: 767px) {
  .questions {
    padding-right: 15px;
    padding-left: 15px;
    padding-top: calc(80px - 30px);
    padding-bottom: 80px;
  }

  .questions_title {
    font-size: 28px;
    line-height: 110%;
    margin-bottom: 30px;
  }

  .questions_item {
    padding: 24px 0;
  }

  .questions_question {
    font-size: 20px;
    line-height: 120%;

    & svg {
      width: 20px;
      height: 10px;
    }
  }

  .question_answer {
    padding-right: 0;
  }

  .quesrions_checkbox:checked~.question_answer {
    padding-top: 24px;
  }
}