.resoult {
  padding-top: 150px;
  padding-bottom: calc(150px - 30px);
  background: url("../img/background2.svg") center 45% no-repeat;
  background-size: 100% auto;

  & .center {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.resoult_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 100%;
  text-align: center;
  color: #11244B;
  margin-bottom: 30px;
  width: 100%;
}

.resoult_subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 130%;
  text-align: center;
  color: #5D6387;
  margin-bottom: 42px;
  width: 100%;
}

.resoult_img {
  flex-basis: 48.7%;
  margin-bottom: 30px;

  & img {
    width: 100%;
    height: 100%;
  }
}

@media(max-width: 990px) {
  .resoult {
    background: url("../img/background2-tablet.svg") center 45% no-repeat;
    background-size: 100% auto;
  }
}

@media(max-width: 767px) {
  .resoult {
    padding-top: 80px;
    padding-bottom: calc(80px - 24px);
    background: url("../img/background2-mob.svg") center 45% no-repeat;
    background-size: 100% auto;
  }

  .resoult_title {
    font-size: 28px;
    line-height: 110%;
    margin-bottom: 20px;
  }

  .resoult_subtitle {
    font-size: 18px;
    line-height: 130%;
    margin-bottom: 30px;
  }

  .resoult_img {
    flex-basis: 100%;
    margin-bottom: 24px;
  }
}