.cost {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 150px;
}

.cost_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 100%;
  text-align: center;
  color: #11244B;
  margin-bottom: 30px;
}

.cost_subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 130%;
  text-align: center;
  color: #5D6387;
  margin-bottom: 30px;
}

.cost_sales-wrapper {
  background: #FFFFFF;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  min-height: 60px;
  margin-bottom: 50px;
}

.cost_radio {
  display: none;
}

.cost_sail {
  background: white;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  text-align: center;
  color: #11244B;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-right: 15px;
  padding-left: 15px;
  border-top: 2px solid #EAEAEA;
  border-bottom: 2px solid #EAEAEA;

  &:first-of-type {
    border-left: 2px solid #EAEAEA;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
  }

  &:last-of-type {
    border-right: 2px solid #EAEAEA;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }

  & span {
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 120%;
    text-align: center;
    color: #11244B;
    background: #FFDB3F;
    border-radius: 4px;
    padding: 6px 7px;
    margin-right: 8px;
  }

  &:hover {
    cursor: pointer;
    color:  #4A50ED;
  }
}

.cost_radio:checked + .cost_sail {
  background: #4A50ED;
  border-color: #4A50ED;
  color: white;

  &:hover {
    color: white;
  }
}

.cost_list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.cost_item {
  flex-basis: 31.6%;
  background: #FFFFFF;
  box-shadow: 0px 6px 40px -10px rgba(74, 80, 237, 0.2);
  border-radius: 6px;
  padding: 30px 40px;
  box-sizing: border-box;
}

.cost_img {
  margin-bottom: 30px;
}

.cost_item-title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 120%;
  color: #11244B;
  margin-bottom: 12px;
}

.cost_item-subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 120%;
  color: #11244B;
  margin-bottom: 34px;
}

.cost_item-text {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 140%;
  color: #5D6387;
  margin-bottom: 24px;
}

.cost_item-button {
  background: #4A50ED;
  border-radius: 6px;
  border: 1px solid #4A50ED;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 90%;
  color: #FFFFFF;
  padding: 22px 0;
  width: 100%;

  &:hover {
    cursor: pointer;
    background: white;
    color: #3A3A3A;
  }
}

@media(max-width: 990px) {
  .cost {
    padding-bottom: calc(150px - 40px);
  }

  .cost_subtitle {
    margin-bottom: 40px;
  }

  .cost_sales-wrapper {
    background: transparent;
    margin-bottom: calc(40px - 20px);
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 125px;
    padding-right: 125px;
  }
  
  .cost_sail {
    border: 2px solid #EAEAEA;
    border-radius: 4px;
    flex-basis: 47.7%;
    min-height: 60px;
    box-sizing: border-box;
    margin-bottom: 20px;
  }

  .cost_item {
    flex-basis: 47.8%;
    margin-bottom: 40px;
  }
}

@media(max-width: 767px) {
  .cost {
    padding-bottom: calc(80px - 25px);
  }
  .cost_title {
    font-size: 28px;
    line-height: 110%;
    margin-bottom: 18px;
  }

  .cost_subtitle {
    font-size: 18px;
    line-height: 130%;
    margin-bottom: 24px;
  }

  .cost_sales-wrapper {
    padding-left: 0;
    padding-right: 0;
    margin-bottom: calc(30px - 10px);
  }

  .cost_sail {
    font-size: 15px;
    line-height: 110%;
    padding: 0 10px;
    margin-bottom: 10px;

    & span {
      font-size: 13px;
      padding: 6px 4px;
    }
  }

  .cost_list {
    flex-direction: column;
    justify-content: flex-start;
  }

  .cost_item {
    padding: 24px 16px;
    margin-bottom: 25px;
  }

  .cost_img {
    width: 60px;
    height: 60px;
    margin-bottom: 24px;
  }

  .cost_item-title {
    font-size: 21px;
    line-height: 120%;
    margin-bottom: 6px;
  }

  .cost_item-subtitle {
    font-size: 24px;
    line-height: 120%;
    margin-bottom: 34px;
  }
}