.description {
  margin-top: 150px;
  margin-bottom: calc(150px - 14px);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.description_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 100%;
  text-align: center;
  color: #11244B;
  margin-bottom: 50px;
  width: 100%;
}

.description_item {
  flex-basis: 19.4%;
  display: flex;
  flex-direction: column;
}

.description_subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 120%;
  color: #11244B;
  margin-bottom: 18px;
}

.description_button {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 90%;
  color: #FFFFFF;
  background: #4A50ED;
  border-radius: 4px;
  border: 1px solid #4A50ED;
  margin-bottom: 32px;
  padding-top: 16px;
  padding-bottom: 16px;
  align-self: flex-start;
  min-width: 150px;

  &:hover {
    cursor: pointer;
    background: white;
    color: #3A3A3A;
  }
}

.description_list {
  margin: 0;
  list-style: none;
  padding-left: 0;
}

.description_point {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 130%;
  color: #5D6387;
  display: flex;
  flex-direction: row;
  margin-bottom: 14px;

  & span {
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 9px;
    line-height: 130%;
    color: #FFFFFF;
    background: #4A50ED;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    flex-shrink: 0;
    margin-right: 10px;
  }
}

.description_modal {
  background: #FFFFFF;
  border-radius: 6px;
  max-width: 470px;
  width: 100%;
  box-sizing: border-box;
  padding-top: 40px;
  padding-bottom: calc(40px - 14px);
  padding-right: 50px;
  padding-left: 50px;

  & .description_subtitle {
    font-size: 28px;
    margin-bottom: 24px;
  }

  & .description_point {
    font-size: 15px;
  }
}

@media(max-width: 990px) {
  .description {
    padding-right: 60px;
    padding-left: 60px;
    margin-bottom: calc(150px - 50px);
  }

  .description_item {
    flex-basis: 47.3%;
    margin-bottom: calc(50px - 14px);
  }
}

@media(max-width: 767px) {
  .description {
    margin-top: calc(80px - 30px);
    flex-direction: column;
    justify-content: flex-start;
    padding-right: 15px;
    padding-left: 15px;
    margin-bottom: calc(80px - 30px);
  }

  .description_title {
    font-size: 28px;
    line-height: 110%;
    margin-bottom: 40px;
  }

  .description_subtitle {
    font-size: 21px;
    line-height: 120%;
    margin-bottom: 14px;
  }

  .description_button {
    margin-bottom: 20px;
  }

  .description_item {
    margin-bottom: calc(30px - 14px);
  }

  .description_modal {
    padding: 25px 15px 13px 15px;

    & .description_subtitle {
      font-size: 21px;
      line-height: 120%;
      margin-bottom: 19px;
    }

    & .description_point {
      margin-bottom: 12px;
    }
  }
}