.additionally {
  margin-bottom: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.additionally_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 28px;
  line-height: 100%;
  text-align: center;
  color: #11244B;
  margin-bottom: 32px;
}

.additionally_tabel {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 10px 80px -30px rgba(74, 80, 237, 0.3);
  border-radius: 6px;
  background: white;
}

.additionally_row {
  display: flex;

  &:not(:first-of-type):not(:last-of-type) {
    border-bottom: 2px solid #EAEAEA;
  }

  &:hover {
    background: #f3f7ff;
  }

  &:first-of-type {
    background: #603EE6;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;

    &:hover {
      background: #603EE6;
    }
  }
}

.additionally_cell {
  padding: 13px 30px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 120%;
  color: #5D6387;
  box-sizing: border-box;
  flex-shrink: 0;

  &--title {
    padding: 21px 30px;
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    color: #FFFFFF;
  }

  &:last-of-type {
    text-align: right;
  }

  &:first-of-type {
    text-align: center;
  }

  &:not(:last-of-type) {
    border-right: 2px solid #EAEAEA;
  }

  &:nth-of-type(1) {
    width: 92px;
  }

  &:nth-of-type(2) {
    width: 422px;
  }

  &:nth-of-type(3) {
    width: 255px;
  }
}

@media(max-width: 990px) {
  .additionally_cell {
    &:nth-of-type(1) {
      width: 80px;
    }
  
    &:nth-of-type(2) {
      width: 380px;
    }
  
    &:nth-of-type(3) {
      width: 230px;
    }
  }
}

@media(max-width: 767px) {
  .additionally {
    overflow-x: scroll;
    margin-bottom: 0;
    -ms-overflow-style: none;
    scrollbar-width: none;
  
    &::-webkit-scrollbar {
      width: 0;
      display: none;
    }
  }

  .additionally_wrapper {
    align-self: flex-start;
    padding: 0 15px 80px 15px;
  }

  .additionally_tabel {
    align-self: flex-start;
  }

  .additionally_cell {
    font-size: 15px;
    flex-grow: 1;

    &--title {
      font-size: 16px;
    }

    &:nth-of-type(1) {
      width: 90px;
    }
  
    &:nth-of-type(2) {
      width: 390px;
    }
  
    &:nth-of-type(3) {
      width: 252px;
    }
  }
}